    /*COLOR PALETTE*/
    
     :root {
        --color-primary: #2584ff;
        --color-secondary: #00d9ff;
        --color-accent: #ff3400;
        --color-headings: #1b0760;
        --color-body: #918ca4;
        --color-border: #ccc;
        --border-radius: 30px;
        --color-body-darker: #5c5577;
    }
    
    *,
    *::after,
    *::before {
        box-sizing: border-box;
    }
    /*TYPOGRAPHY*/
    
     ::selection {
        background-color: var(--color-primary);
        color: #fff;
    }
    
    html {
        /*62.5% of 16px = 10px*/
        font-size: 62.5%;
    }
    
    body {
        font-family: Inter, Arial, Helvetica, sans-serif;
        font-size: 2.4rem;
        line-height: 1.5;
        color: var(--color-body);
    }
    
    h1,
    h2,
    h3 {
        color: var(--color-headings);
        margin-bottom: 1rem;
        line-height: 1.1;
    }
    
    h1 {
        font-size: 7rem;
    }
    
    h2 {
        font-size: 4rem;
    }
    
    h3 {
        font-size: 3rem;
        font-weight: 500;
    }
    
    p {
        margin-top: 0;
    }
    /*PC TYPOGRAPHY*/
    
    @media screen and (min-width:1024px) {
        body {
            font-size: 1.8rem;
        }
        h1 {
            font-size: 8rem;
        }
        h2 {
            font-size: 4rem;
        }
        h3 {
            font-size: 2.4rem;
        }
    }
    /*LINKS*/
    
    a {
        text-decoration: none;
    }
    
    .link-arrow {
        color: var(--color-accent);
        text-transform: uppercase;
        font-size: 2rem;
        font-weight: bold;
    }
    
    .link-arrow::after {
        content: "-->";
        margin-left: 5px;
        transition: margin 0.15s;
    }
    
    .link-arrow:hover::after {
        margin-left: 10px;
    }
    
    @media screen and (min-width:1024px) {
        .link-arrow {
            font-size: 1.5rem;
        }
    }
    /*BADGES*/
    
    .badge {
        border-radius: 20px;
        font-size: 2rem;
        font-weight: 600;
        padding: 0.5rem 2rem;
        white-space: nowrap;
    }
    
    .badge--primary {
        background: var(--color-primary);
        color: #fff;
    }
    
    .badge--secondary {
        background: var(--color-secondary);
        color: #fff;
    }
    
    .badge--small {
        font-size: 1.6rem;
        padding: 0.5rem 1.5rem;
    }
    
    @media screen and (min-width:1024px) {
        .badge {
            font-size: 1.5rem;
        }
        .badge--small {
            font-size: 1.2rem;
        }
    }
    /*LISTS*/
    
    .list {
        list-style: none;
        padding-left: 0;
    }
    
    .list--inline .list__item {
        display: inline-block;
        margin-right: 2rem;
    }
    
    .list--tick {
        list-style-image: url(../icons/tick.svg);
        padding-left: 3rem;
        color: var(--color-headings);
    }
    
    .list--tick .list__item {
        padding-left: 0.5rem;
        margin-bottom: 1rem;
    }
    
    @media screen and (min-width:1024px) {
        .list--tick .list__item {
            padding-left: 0;
        }
    }
    /*ICONS*/
    
    .icon {
        width: 40px;
        height: 40px;
    }
    
    .icon--small {
        width: 30px;
        height: 30px;
    }
    
    .icon--primary {
        fill: var(--color-primary);
    }
    
    .icon--secondary {
        fill: var(--color-secondary);
    }
    
    .icon--accent {
        fill: #fff;
    }
    
    .icon-container {
        background: #f3f9fa;
        width: 64px;
        height: 64px;
        border-radius: 100%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
    }
    
    .icon-container--accent {
        background: var(--color-accent);
        width: 64px;
        height: 64px;
        border-radius: 100%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
    }
    /*BUTTONS*/
    
    .button-subscribe {
        background-color: #fff;
        border-radius: 5rem;
        cursor: pointer;
        font-size: 1.8rem;
        font-weight: 600;
        outline: 0;
        margin: 1rem 1rem 3rem 1rem;
        padding: 2rem 4vw;
        text-transform: uppercase;
        white-space: nowrap;
        width: 70%;
    }
    
    button .icon {
        width: 2rem;
        height: 2rem;
        margin-right: 1rem;
        transform: translateY(4px);
        /*vertical-align: middle;*/
    }
    
    .button-subscribe-callout {
        background-color: #fff;
        border-radius: 5rem;
        cursor: pointer;
        font-size: 1.8rem;
        font-weight: 600;
        margin: 1rem 1rem 3rem 1rem;
        padding: 2rem 3rem;
        text-transform: uppercase;
        white-space: nowrap;
    }
    
    .button--primary {
        background-color: var(--color-primary);
        color: #fff;
        border: 0;
    }
    
    .button--primary:hover {
        background: #3a8ffe;
    }
    
    .button--secondary {
        background-color: var(--color-secondary);
        color: #fff;
        border: 0;
    }
    
    .button--secondary:hover {
        background: #00c8eb;
    }
    
    .button--outline {
        background-color: #fff;
        color: var(--color-headings);
        border: 2px solid var(--color-headings);
    }
    
    .button--outline:hover {
        background-color: var(--color-headings);
        color: #fff;
    }
    
    .button-container {
        align-items: flex-end;
        border: 3px solid gray;
        display: flex;
        height: 300px;
        justify-content: center;
        width: 300px;
    }
    
    .button-container-search {
        align-items: flex-end;
        border: 3px solid gray;
        display: flex;
        height: 300px;
        justify-content: flex-end;
        width: 300px;
    }
    
    .button--search {
        background-color: var(--color-accent);
        border-radius: 5rem;
        border: 0;
        color: #fff;
        cursor: pointer;
        margin-bottom: 3rem;
        margin: 2rem;
        outline: 0;
        padding: 2rem 3rem;
        text-transform: uppercase;
        white-space: nowrap;
        width: 40%;
    }
    
    .button--search:hover {
        background: #ec3000;
    }
    
    .button--stretched {
        padding-left: 6rem;
        padding-right: 6rem;
    }
    
    .button--block {
        width: 100%;
        display: inline-block;
    }
    
    @media screen and (min-width:1024px) {
        .button-subscribe {
            font-size: 1.5rem;
        }
    }
    /*INPUTS*/
    
    .input-domain {
        border-radius: var(--border-radius);
        border: 1px solid var(--color-border);
        box-shadow: gray;
        color: var(--color-headings);
        font-size: 2rem;
        outline: 0;
        padding: 1.5rem 3.5rem;
    }
    
     ::placeholder {
        color: #cdcbd7;
    }
    
    @media screen and (min-width:1024px) {
        .input-domain {
            font-size: 1.5rem;
        }
    }
    /*INPUT GROUPS*/
    
    .input-group {
        border: 1px solid var(--color-border);
        border-radius: var(--border-radius);
        margin: 5rem;
        display: flex;
    }
    
    .input-group .input-domain {
        border: 0;
        flex-grow: 1;
        padding: 1.5rem 2rem;
        color: var(--color-headings);
        width: 0;
    }
    
    .input-group .button--search {
        margin: 4px;
    }
    /*CARDS*/
    
    .card {
        border-radius: 7px;
        box-shadow: 0 0 20px 10px #f3f3f3;
        overflow: hidden;
        margin: 5rem;
        min-width: 35rem;
    }
    
    .card__header,
    .card__body {
        padding: 2rem 3rem;
    }
    
    .card--primary .card__header {
        background-color: var(--color-primary);
        color: #fff;
    }
    
    .card--secondary .card__header {
        background-color: var(--color-secondary);
        color: #fff;
    }
    
    .card--primary .badge--primary {
        background-color: #126de4;
    }
    
    .card--secondary .badge--secondary {
        background-color: #02cdf1;
    }
    /*PLANS*/
    
    .plan__name {
        margin: 0;
        color: #fff;
        font-weight: 500;
        font-size: 2.4rem;
    }
    
    .plan__price {
        font-size: 6rem;
    }
    
    .plan__billing-cycle {
        font-size: 2.4rem;
        font-weight: 300;
        opacity: 0.8;
        margin-right: 1rem;
    }
    
    .plan__description {
        display: block;
        font-size: 2rem;
        font-weight: 300;
        letter-spacing: 1px;
    }
    
    .card__body {
        letter-spacing: 1px;
    }
    
    .list__item {
        margin-bottom: 2rem;
    }
    
    @media screen and (min-width:1024px) {
        .plan__name {
            font-size: 1.4rem;
        }
        .plan__price {
            font-size: 5rem;
        }
        .plan__billing-cycle {
            font-size: 1.6rem;
        }
        .plan__description {
            font-size: 1.7rem;
        }
    }
    /*POPULAR BADGE*/
    
    .plan--popular .card__header {
        position: relative;
    }
    
    .plan {
        transition: transform .2s ease-out;
    }
    
    .plan--popular {
        transform: scale(1.1);
    }
    
    .plan--popular .card__header::before {
        content: url(../icons/popular.svg);
        width: 40px;
        display: inline-block;
        position: absolute;
        top: -6px;
        right: 5%;
    }
    
    .plan:hover {
        transform: scale(1.05);
    }
    
    .plan--popular:hover {
        transform: scale(1.15);
    }
    /*MEDIA OBJECTS*/
    
    .media {
        display: flex;
        margin-bottom: 4rem;
    }
    
    .media__title {
        margin-top: 0;
    }
    
    .media__body {
        margin: 0 2rem;
    }
    
    .media__image {
        margin: 1rem 0 0 1rem;
    }
    /*QUOTES*/
    
    .quote {
        font-size: 3rem;
        font-style: italic;
        color: var(--color-body-darker);
        line-height: 1.3;
    }
    
    .quote__text::before {
        content: open-quote;
    }
    
    .quote__text::after {
        content: close-quote;
    }
    
    .quote__author {
        font-size: 3rem;
        font-weight: 500;
        font-style: normal;
        margin-bottom: 0;
    }
    
    .quote__compagny {
        color: var(--color-headings);
        opacity: 40%;
        font-size: 2rem;
        font-style: normal;
    }
    
    .quote__line {
        position: relative;
        bottom: 10px;
    }
    
    @media screen and (min-width:1024px) {
        .quote {
            font-size: 2rem;
        }
        .quote__author {
            font-size: 2.4rem;
        }
        .quote__organization {
            font-size: 1.6rem;
        }
    }
    /*GRIDS*/
    
    .grid {
        display: grid;
    }
    
    @media screen and (min-width:768px) {
        .grid--1x2 {
            grid-template-columns: 1fr 1fr;
        }
    }
    
    @media screen and (min-width:1024px) {
        .grid--1x3 {
            grid-template-columns: 1fr 1fr 1fr;
            justify-content: center;
        }
    }
    /*TESTIMONIALS*/
    
    .test {
        padding: 3rem;
    }
    
    .test__image {
        position: relative;
    }
    
    .test__image>.icon-container {
        position: absolute;
        top: 3rem;
        right: -32px;
    }
    
    .test__image>img {
        width: 100%;
    }
    
    @media screen and (min-width:768px) {
        .test .quote__text,
        test .quote__author {
            font-size: 2.4rem;
        }
        .test .quote {
            margin-left: 6rem;
        }
    }
    /*CALLOUTS*/
    
    .callout {
        padding: 4rem;
        border-radius: 5px;
    }
    
    .callout--primary {
        background-color: var(--color-primary);
        color: #fff;
    }
    
    .callout__heading {
        color: #fff;
        margin-top: 0;
        font-size: 3rem;
    }
    
    .callout .button-subscribe-callout {
        justify-self: center;
        align-self: center;
        text-align: center;
    }
    
    .callout__content {
        text-align: center;
    }
    
    @media screen and (min-width:768px) {
        .callout .grid--1x2 {
            grid-template-columns: 1fr auto;
        }
        .callout__content {
            text-align: left;
        }
        .callout .button-subscribe-callout {
            justify-self: start;
            margin: 0 2rem;
        }
    }
    /*COLLAPSIBLES*/
    
    .collapsible__header {
        display: flex;
        justify-content: space-between;
    }
    
    .collapsible__heading {
        margin-top: 0;
        font-size: 3rem;
    }
    
    .collapsible__chevron {
        transform: rotate(-90deg);
        transition: transform 0.3s;
    }
    
    .collapsible__content {
        max-height: 0;
        overflow: hidden;
        opacity: 0;
        transition: all 0.3s;
    }
    
    .collapsible--expanded .collapsible__chevron {
        transform: rotate(0);
    }
    
    .collapsible--expanded .collapsible__content {
        max-height: 100vh;
        opacity: 1;
    }
    /*BLOCKS*/
    
    .block {
        padding: var(--padding-vertical) 2rem;
        --padding-vertical: 6rem;
    }
    
    .block__header {
        text-align: center;
        margin-bottom: 4rem;
    }
    
    .block__heading {
        margin-top: 0;
        font-size: 10rem;
    }
    
    .block--dark {
        background-color: #000;
        color: #7b858b;
    }
    
    .block--dark h1,
    .block--dark h2,
    .block--dark h3 {
        color: #fff;
    }
    
    .block--skewed-right {
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 90%);
        padding-bottom: calc(var(--padding-vertical) + 4rem);
    }
    
    .block--skewed-left {
        clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
        padding-bottom: calc(var(--padding-vertical) + 4rem);
    }
    
    .container {
        max-width: 1140px;
        margin: 0 auto;
    }
    /*NAVIGUATION BAR*/
    
    .nav {
        background-color: #000;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 0 1rem;
        align-items: center;
    }
    
    .nav__list {
        width: 100%;
        margin: 0;
    }
    
    .nav__item {
        padding: 0.5rem 2rem;
        border-bottom: 1px solid #222;
    }
    
    .nav__item>a {
        color: #d2d0db;
        transition: color .3s;
    }
    
    .nav__item>a:hover {
        color: #fff;
    }
    
    .nav__toggler {
        opacity: .5;
        transition: box-shadow .15s;
        cursor: pointer;
    }
    
    .nav.collapsible--expanded .nav__toggler {
        opacity: 1;
        box-shadow: 0 0 0 3px #666;
        border-radius: 5px;
    }
    
    .nav__logo {
        transform: translateY(5px);
    }
    
    @media screen and (min-width:768px) {
        .nav__toggler {
            display: none;
        }
        .nav__list {
            width: auto;
            display: flex;
            font-size: 1.6rem;
            max-height: 100%;
            opacity: 1;
        }
        .nav__item {
            border-bottom: none;
        }
    }
    /*HERO*/
    
    .hero__heading {
        font-size: 8rem;
    }
    
    .hero__tagline {
        font-size: 2rem;
        color: #b9c2cf;
        letter-spacing: 1px;
        margin: 2rem 0 5rem;
    }
    
    .hero__banner {
        width: 100%;
    }
    
    @media screen and (min-width:768px) {
        .hero {
            padding-top: 0;
        }
        .hero__content {
            text-align: left;
            align-self: center;
        }
    }
    /*DOMAIN BLOCK*/
    
    .block-domain .input-group {
        box-shadow: 0 0 30px 20px #e6ebee;
        border: 0;
        margin: 4rem auto;
        max-width: 630px;
    }
    
    .block-domain__price {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 6rem);
        justify-items: center;
        font-size: 2rem;
        font-weight: 600;
        max-width: 730px;
        margin: 0 auto;
        color: var(--color-headings);
    }
    
    @media screen and (min-width:768px) {
        .block-domain__price {
            grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
        }
    }
    /*Plans block*/
    
    .plan-block .grid {
        gap: 8rem 4rem;
    }
    
    .block-plans .card {
        max-width: 500px;
        margin: 0 auto;
    }
    /*FEATURES*/
    
    .feature {
        gap: 4rem 2rem;
        margin: 12rem 0;
    }
    
    .feature__heading {
        margin: 1rem 0;
    }
    
    .feature__image {
        width: 100%;
    }
    
    .feature:first-of-type {
        margin-top: 6rem;
    }
    
    .article__feature {
        text-align: center;
    }
    /*

    Technique pour inverser chaque article de manière automatique

    @media screen and (min-width:768px) {
        .feature:nth-of-type(even) .feature__content {
            order: 2;
        }
    }
    */
    /*SHOWCASE BLOCK*/
    
    .block__heading {
        text-align: center;
        font-size: 4rem;
    }
    
    .block-showcase__image>img {
        width: 100%;
    }
    
    .block-showcase__image>img {
        width: auto;
        max-width: 700px;
    }
    
    @media screen and (min-width:768px) {
        .block-showcase__image {
            justify-self: right;
        }
        .block-showcase .grid {
            grid-template-columns: 50% 50%;
        }
    }
    /*FOOTER*/
    
    .footer {
        background-color: #232323;
    }
    
    .footer a {
        color: #777;
        transition: color 0.3s;
    }
    
    .footer a:hover {
        cursor: pointer;
        color: #fff;
    }
    
    .footer__section {
        padding: 2rem;
        border-bottom: 1px solid #393939;
    }
    
    .footer__section .list {
        margin: 0;
    }
    
    .footer__heading {
        text-transform: uppercase;
        font-style: 600;
    }
    
    .footer__brand {
        margin-top: 5rem;
        text-align: center;
    }
    
    .footer__brand img {
        /*TODO: Consider refactoring this and applying it to all images*/
        width: 100%;
        max-width: 230px;
    }
    
    .footer__copyright {
        font-size: 2.1rem;
        color: #fff;
        opacity: 0.3;
    }
    
    @media screen and (min-width:768px) {
        .footer__sections {
            grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
        }
        .footer .collapsible__chevron {
            display: none;
        }
        .footer .collapsible__content {
            opacity: 1;
            max-height: 100%;
        }
        .footer__brand {
            order: -1;
        }
        .footer__copyright {
            font-size: 1.5rem;
        }
        .footer__brand {
            margin-top: 1rem;
        }
        .footer__section {
            border: none;
        }
        .footer__heading {
            font-size: 1.6rem;
        }
    }